import { PlayAudio } from "@/lib/svg-imports"
import styles from "./Paragraph.module.css"
import Image from "next/image"

export default function Paragraph( {
  children,
  imgSrc,
  imgAlt,
  imgPosition = "left"
   }) {
  const hasImage = Boolean(imgSrc)

  return (
    <div className={[
      hasImage && styles.withImage,
      hasImage && styles[imgPosition]
      ]
      .filter(Boolean)
      .join(" ")}>
        {hasImage && (
          <Image
        src={imgSrc}
        alt={imgAlt}
        width={200}
        height={200}
        className={styles.image}
        />
        )}
      <p><PlayAudio className={styles.PlayAudio}/>{children}</p>
    </div>
  )
}